var html = require('choo/html')
var raw = require('choo/html/raw')
var Nanocomponent = require('nanocomponent')
var css = 0


var sheetify_document = "<header>\n  <h2>awoo document</h2>\n  <small>JANUARY 2, 2017</small>\n</header>\n<section class='article'>\n  <div class=\"markdown-body\">\n  <p><strong>Table of Contents</strong></p>\n  <ul>\n  <li><a href=\"#installation\">Installation</a></li>\n  <li><a href=\"#example\">Example</a></li>\n  <li><a href=\"#how-does-it-work\">How does it work?</a></li>\n  <li><a href=\"#api-documentation\">API Documentation</a></li>\n  <li><a href=\"#plugins\">Plugins</a></li>\n  <li><a href=\"#development-setup\">Development Setup</a></li>\n  <li><a href=\"#how-does-it-compare\">How does it compare?</a></li>\n  <li><a href=\"#what-dependencies-does-it-have\">What dependencies does it have?</a></li>\n  <li><a href=\"#license\">License</a></li>\n  </ul>\n  <h2>Features</h2>\n  <ul>\n  <li><strong>extremely simple</strong>: the API exposes <em>one function</em>!</li>\n  <li><strong>easy to understand</strong>: only ~180 SLOC!</li>\n  <li><strong>very modern</strong>: works very well with async functions (node 7.6+)</li>\n  <li><strong>fast as lightning</strong>: because it's so simple, it takes almost no time to build a site!</li>\n  </ul>\n  <h2>Installation</h2>\n  <p>You need Node.js <strong>7.6</strong> or higher.</p>\n  <pre class=\"language-sh\">\n  <code class=\"language-sh\">npm install --save awoo</code></pre>\n  <h2>Example</h2>\n  <p>Let's write a simple program that changes the content of all files in a\n  directory to &quot;hey, what's up&quot;:</p>\n  <pre class=\"language-js\">\n  <code class=\"language-js\"><span class=\"token keyword\">const</span> awoo <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'awoo'</span><span class=\"token punctuation\">)</span>\n  \n  <span class=\"token comment\">// this is this simplest plugin you can build!</span>\n  <span class=\"token comment\">// conveniently, plugins are just normal functions</span>\n  <span class=\"token keyword\">const</span> <span class=\"token function-variable function\">plugin</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// replace all file contents with the string</span>\n    <span class=\"token keyword\">return</span> files <span class=\"token operator\">=></span> files<span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span>file <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n      file<span class=\"token punctuation\">.</span>contents <span class=\"token operator\">=</span> <span class=\"token template-string\"><span class=\"token string\">`hey, what's up`</span></span>\n      <span class=\"token keyword\">return</span> file\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n  \n  <span class=\"token comment\">// enter our main function:</span>\n  <span class=\"token comment\">// the main function should be an async function so that</span>\n  <span class=\"token comment\">// it automatically returns a promise</span>\n  <span class=\"token function\">awoo</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">async</span> site <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// we register our plugin...</span>\n    site<span class=\"token punctuation\">.</span><span class=\"token function\">use</span><span class=\"token punctuation\">(</span>plugin<span class=\"token punctuation\">)</span>\n    <span class=\"token comment\">// ...and initiate the build process</span>\n    <span class=\"token keyword\">return</span> site\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span></code></pre>\n  <p>Let's save this as <code>example.js</code>. To run it, you need Node.js version 7.6 or\n  higher. The latest stable version will work.</p>\n  <pre class=\"language-sh\">\n  <code class=\"language-sh\">node example.js</code></pre>\n  <p><code>awoo</code> doesn't output logs by default. If you want logs\n  (for example, for debugging), you can set the <code>DEBUG</code> environment variable to:</p>\n  <pre class=\"language-sh\">\n  <code class=\"language-sh\">awoo # for base logs\n  awoo:read # for specific read logs\n  awoo:write # for specific write logs\n  \n  # you can also combine any of the three\n  awoo,awoo:read</code></pre>\n  <p>To get all logs at once, you can just set the variable to <code>awoo*</code>.</p>\n  <pre class=\"language-bash\">\n  <code class=\"language-bash\">DEBUG<span class=\"token operator\">=</span>awoo* node my_script <span class=\"token comment\"># full logging!</span></code></pre>\n  <p><code>awoo</code> plugins may implement logging with different <code>DEBUG</code> names.</p>\n  <h2>How does it work?</h2>\n  <p>It's fairly simple! What <code>awoo</code> does can be split up into two things:</p>\n  <ul>\n  <li>First, it reads a directory and saves all of the information about each file\n  into a gigantic array. That object can be manipulated by <em>plugins</em>, which\n  makes <code>awoo</code> actually do things.</li>\n  <li>After most plugins are run, <code>awoo</code> writes the files as they are described\n  in the gigantic array to disk.</li>\n  </ul>\n  <p>It's that simple! Static site generators aren't rocket science or whatever.\n  If you got confused by looking at the Jekyll source code once, that's because\n  Jekyll is more fully fledged than <code>awoo</code> is, that is, it provides some defaults.</p>\n  <p>But static site generators, at their core, are just programs that take a set of\n  files, do something to them, and then output those files. That's it. The rest is just <em>transformations</em> on those files.</p>\n  <p><code>awoo</code>'s goal is to reduce that essence to its very base, and to give you a\n  bunch of building blocks with which you can make your ideal site, using only\n  the stuff you need!</p>\n  <h2>API Documentation</h2>\n  <p>The <a href=\"https://awoo.js.org/docs\">docs</a> are published on our website.</p>\n  <h2>Plugins</h2>\n  <p>Official plugins are kept at <a href=\"http://github.com/awoojs/core\">awoojs/core</a>!</p>\n  <p>Since it's really easy to write <code>awoo</code> plugins, anyone can make and publish one!\n  If you make a plugin, you should add a GitHub topic <code>awoo</code> and probably also\n  add <code>awoo</code> as a keyword in your <code>package.json</code>.</p>\n  <p><a href=\"https://github.com/search?utf8=%E2%9C%93&amp;q=topic%3Aawoo&amp;type=\">Here's a list of all awoo plugins on GitHub</a></p>\n  <h2>Development</h2>\n  <p>To work on this repository, clone it and install the npm dependencies:</p>\n  <pre class=\"language-sh\">\n  <code class=\"language-sh\">git clone https://github.com/awoojs/awoo.git\n  cd awoo\n  npm install</code></pre>\n  <p>There are a couple of npm scripts provided for convenience:</p>\n  <ul>\n  <li><code>npm test</code> - runs linters and ava in ci mode</li>\n  <li><code>npm run lint</code> - runs linters</li>\n  <li><code>npm run ava</code> - only runs ava once</li>\n  <li><code>npm run ava:ci</code> - runs ava in ci mode (generates coverage data)</li>\n  <li><code>npm run ava:watch</code> - runs ava in watch mode</li>\n  <li><code>npm run coverage</code> - generates coverage data</li>\n  <li><code>npm run update-coc</code> - pulls the latest <code>weallbehave</code> code of conduct</li>\n  <li><code>npm run deploy</code> - publishes npm package using <code>np</code></li>\n  </ul>\n  <h2>How does it compare?</h2>\n  <p>This section is a little bit about how <code>awoo</code> compares to other static site\n  generators (even though it isn't really that):</p>\n  <ul>\n  <li><strong>jekyll</strong>: Jekyll is a whole lot different. First off, it provides a whole\n  lot of defaults (such as YAML Front-Matter, the Liquid language, etc) to\n  help you build your static site extremely quickly. It's also massively\n  geared for security, since it runs on GitHub's Pages platform. Stuff like\n  custom plugins isn't even available there (not like that's a bad thing!).</li>\n  <li><strong>hugo</strong>: Hugo, a bit like Jekyll, has predefined concepts like &quot;pages&quot; and\n  &quot;tags&quot;.</li>\n  <li><strong>metalsmith</strong>: Metalsmith is probably the thing that's most like <code>awoo</code>, and\n  as a matter of fact, its main inspiration. It's also plugin-based, and works\n  with roughly the same concepts. The major difference is that <code>awoo</code> is more\n  up-to-date (I like promises a lot) and that the API is fairly different.\n  Also, it's just not really an active project with an active ecosystem\n  anymore (sadly!).</li>\n  </ul>\n  <h2>What dependencies does it have?</h2>\n  <ul>\n  <li><a href=\"http://npm.im/deepmerge\"><code>deepmerge</code></a> - used to handle configuration management</li>\n  <li><a href=\"http://npm.im/walk\"><code>walk</code></a> - walks through directories to read them</li>\n  <li><a href=\"http://npm.im/trough\"><code>trough</code></a> - handles middleware chains</li>\n  <li><a href=\"http://npm.im/debug\"><code>debug</code></a> - used for logging</li>\n  <li><a href=\"http://npm.im/to-vfile\"><code>to-vfile</code></a> - converts to <code>vfile</code>, the virtual file format used by <code>awoo</code></li>\n  <li><a href=\"http://npm.im/mkdirp\"><code>mkdirp</code></a> - creates directories when writing to disk</li>\n  <li><a href=\"http://npm.im/is-text-path\"><code>is-text-path</code></a> - provides logic to correctly read binary files</li>\n  </ul>\n  <p>If you have any ideas as to how to eliminate a dependency, you're more than\n  welcome to pitch it in a new issue!</p>\n  <h2>Maintainers</h2>\n  <ul>\n  <li>Olivia Hugger &lt;<a href=\"mailto:olivia@fastmail.com\">olivia@fastmail.com</a>&gt;</li>\n  </ul>\n  <h2>Code of Conduct</h2>\n  <p>This repository operates under the <a href=\"https://github.com/wealljs/weallbehave\"><code>weallbehave</code></a> Code of Conduct. Its contents can be found in <a href=\"CODE_OF_CONDUCT.md\"><code>CODE_OF_CONDUCT.md</code></a>.</p>\n  <h2>License</h2>\n  <p>GNU AGPLv3 (see LICENSE document)</p>\n  </div>\n  \n</section>"

